.resume{
    padding-top: 120px;
    background: #000000;
    &__wrapper{
        padding:60px 65px 0 65px;
        display: grid;
        grid-template-columns: 3fr 3fr;
        //grid-template-columns: repeat(2, 525px);
        column-gap: 135px;
    }
    .divider {
        width: 8%;
        height: 3px;
        background-color: #ffa501;
        margin: 25px auto;
    }
    &__column {
        &-title{
            position: relative;
            padding-left: 35px;
            z-index: 2;
            &::before {
                content: '';
                position: absolute;
                width: 15px;
                height: 15px;
                background: #ffa501;
                border-radius: 100%;
                left: -7px;
                top: 50%;
                transform: translateY(-50%);
            }
        }
        ul {
            list-style-type: none;
            li {
                position: relative;
                &:after {
                    content: '';
                    width: 1px;
                    height: calc(100% + 16px );
                    position: absolute;
                    top: -16px;
                    left: 0;
                    background-color: #0467a0;
                }
            } 
        }
    }
    &__item{
        position: relative;
        &::before{
            content: '';
            height: 1px;
            width: 45px;
            position: absolute;
            top: 68px;
            background-color: #0467a0;
        }
        &-head{
            position: relative;
            padding: 45px 0 0 100px;
            h4{
                margin-bottom: 0;
            }
        }
        &-body{
            padding-left: 43px;
            margin-top: 12px;

            font-size: 12px;
            line-height: 18px;
            color: #c5c3c3;
            //color: $text-color;
        }
        &-icon{
            position: absolute;
            top: 50px;
            left: 125px;
            //@include before_circle;
            &::before{
                content: '';
                position: absolute;
                z-index: 1;
                top: 3px;
                right: 50px;
                width: 36px;
                height: 36px;
                border-radius: 100%;
                background: #ffa501;
            }
            img{
                position: absolute;
                left: -80px;
                top: 10px;
                z-index: 1;
            }
        }
        &-location{
            font-size: 12px;
            line-height: 18px;
            margin-top: 5px;
            color: #a609ac;
            //color:$dop-style;
        }
    }
}


.skills {
    padding-top: 60px;
    &__items {
        margin-top: 0px;
        padding: 0 10px;
        display: grid;
        grid-template-columns: 4fr 4fr 4fr;
       // grid-template: repeat(2, minmax(320px, auto)) / repeat(4, 320px);
        grid-auto-rows: minmax(220px, auto);
        gap: 20px;
    }
    .w-20 {
        width: 20%;
    }
    &__item {
        border: 1px solid #0467a0;
        box-shadow: 0 0px 2px #FFA501, 0 2px 2px #FFA501;
        padding: 30px 30px 25px 30px;
        transition: all .2s ease;
        &:hover {
            box-shadow:  0px 4px 15px  rgba(255, 255, 255, 0.596);
        }
        &-img {
            display: flex;
            align-items: center;
            min-height: 65px;
            max-width: 60%;
            img {
                width: 40%;
                height: 30%;
            }
        }
        &-canvas {
            img {
                background-color: #fff;
            }
        }
        h3 {
            margin-top: 20px;
            margin-bottom: 0;
        }
        p {
            margin-top: 15px;
            margin-bottom: 0;
            font-size: 12px;
            line-height: 18px;
            color: #c5c3c3;
        }
    }
}

@media (max-width: 1200px) {
    .resume {
        &__wrapper {
            padding: 40px 20px 0 20px;
            column-gap: 80px;
        }
    }
    .skills {
        &__items {
            grid-template-columns: repeat(auto-fit, 270px);
            justify-content: space-between;
        }
    }
}

@media (max-width: 992px) {
    .resume {
        &__wrapper {
            column-gap: 60px;
            grid-template-columns: repeat(2,320px);
        }
    }
    .skills {
        &__items {
            padding: 0 20px;
            justify-content: center;
            grid-template-columns: 3fr 3fr 3fr;
        }
    }
}

@media (max-width: 768px) {
    .resume {
        padding-top: 40px;
        &__wrapper {
            grid-template-columns: 1fr;
            column-gap: 60px;
            row-gap: 40px;
        }
    }
    .skills {
        padding-top: 40px;
        &__items {
            padding: 0;
            justify-content: center;
            gap: 20px;
        }
    }
}

@media (max-width: 576px) {
    .resume {
        &__wrapper {
            padding: 40px 5px 0 5px;
        }
    }
    .skills {
        &__items {
            margin-top: 20px;
            grid-template-columns: 1fr;
        }
    }
}