.skills-animate {
    padding-top: 120px;
    background: #000000;
    .skills__ratings {
        display: grid;
        grid-template: repeat(2, minmax(70px, auto)) / repeat(3, 380px);
        //grid-template-columns: 3fr 3fr 3fr;
        column-gap: 45px;
        row-gap: 35px;
        padding-left: 50px;
        &-item {
            display: grid;
            grid-template: minmax(21px, auto) 14px / 200px 35px;
            justify-content: space-between;
            align-content: space-between;
        }
        &-title {
            position: relative;
            padding: 2px 0 0 13px;
            color:#0467a0;
            z-index: 2;
            &::before{
                content: '';
                position: absolute;
                z-index: -1;
                top: -25%;
                left: 0%;
                width: 36px;
                height: 36px;
                border-radius: 100%;
                background: #FFA501;
            }
        }
        &-counter {
            font-size: 14px;
            line-height: 21px;
            color: #c5c3c3;
        }
        &-line {
            width: 350px;
            height: 14px;
            background-color: #E8E4DE;
            border-radius: 4px;
            grid-column: 1 / 3;
            span {
                display: block;
                height: 100%;
                width: 50%;
                background-color: #FFA501;
                border-radius: 4px;
            }
        }
    }
}

@media (max-width: 1440px) {
    .skills-animate {
        .skills__ratings {
            grid-template-columns: repeat(auto-fit, 350px);
            grid-auto-rows: minmax(70px, auto);
            justify-content: space-between;
            padding: 0 30px;
        }
    }
}

@media (max-width: 1200px) {
    .skills-animate {
        .skills__ratings {
            grid-template-columns: repeat(auto-fit, 350px);
            grid-auto-rows: minmax(70px, auto);
            justify-content: space-between;
            padding: 0 30px;
        }
    }
}

@media (max-width: 992px) {
    .skills-animate {
        .skills__ratings {
            grid-template-columns: repeat(auto-fit, 320px);
            grid-auto-rows: minmax(70px, auto);
            justify-content: center;
            padding: 0;
            &-line {
                width: 100%;
            }
            
        }
    }
}
@media (max-width: 768px) {
    .skills-animate {
        .skills__ratings {
            grid-template-columns: repeat(auto-fit, 320px);
            justify-content: center;
            margin-top: 40px;
            &-line {
                width: 100%;
            }
        }
    }
}

@media (max-width: 576px) {
    .skills-animate {
        padding-top: 60px;
        .skills__ratings {
            grid-template-columns: repeat(auto-fit,300px);
        }
    }
}

@media (max-width: 350px) {
    .skills-animate {
        .skills__ratings {
            grid-template-columns: repeat(auto-fit,250px);
        }
    }
}