.brief {
    margin-top: 120px;
    //min-height: 800px;
    .title__section-subtitle {
        color: #000;
        margin-bottom: 30px;
    }
    &-go_to {
       
        text-decoration: none;
        color: #139ae9;
        border-bottom: 3px solid #ffa501;
        transition: all .5s;
        &:hover {
            text-decoration: none;
            color: #ffa501;
            border-bottom: none;
        }
    }
    &-wrapper {
       display: grid;
       grid-template-columns: 2fr 2fr;
       column-gap: 50px;
    }
    &-links {
        display: flex;
        justify-content: space-between;
        width: 50%;
        a {
            text-decoration: none;
            color:#ffa501;
            border-bottom: 1px solid #139ae9;
            transition: all .5s;
            &:hover {
                text-decoration: none;
                color: #139ae9;
                border-bottom: none;
            }
        }
    }
}

.carousel-item {
    height: 300px!important;
}

@media (max-width: 1200px) {
    .brief {
        &-links {
            width: 100%;
        }
    }
}

@media (max-width: 992px)  {
    .brief {
        height: 550px;
        &-wrapper {
            padding:0 20px 0 20px;
            grid-template-columns: 1fr;
            row-gap: 50px;
        }
    }
}