.about {
    padding-top: 120px;
    overflow: hidden;
    background: #000000;
    &__wrapper{
        display: grid;
        grid-template-columns: 400px 670px;
        grid-template-rows:minmax(245px, auto) minmax(225px, auto);
        grid-auto-rows:minmax(245px, auto);
        column-gap: 70px;
        row-gap: 40px;
    }
    &__photo{
        grid-row: 1/3;
        position: relative;
        width: 100%;
        &-border {
            position: absolute;
            left: 20px;
            top: 10px;
            //position: relative;
            width: 100%;
            height: 72%;
            display: block;
            border: 5px solid #ffffffb9;
        }
        img{
            border-radius: 6px;
            width: 100%;
        }
    }
    &__descr{
        padding-top: 6px;
        .divider{
            margin: 0px;
        }
    }
    .divider {
        width: 23%;
        height: 3px;
        background-color: #ffa501;
    }
    &__title{
        position: relative;
        padding-left: 12px;
        padding-top: 1px;
        color: #0467a0;
        z-index: 2;
        &::before{
            content: '';
            position: absolute;
            z-index: -1;
            top: -25%;
            left: 0%;
            width: 36px;
            height: 36px;
            border-radius: 100%;
            background: #FFA501;
        }
    }
    &__text{
        margin-top: 20px;
        font-weight: 500;
        font-size: 17px;
        line-height: 1.5;
        color: #c5c3c3e8;
        //color: $text-color;
        span {
            color: #fff;
        }
    }
    &__skills{
        display: grid;
        grid-template: repeat(2, minmax(115px, auto)) / repeat(2, 260px);
        grid-auto-rows: 1fr;
        column-gap: 50px;
        row-gap: 20px;
        &-item{
            display: grid;
            grid-template-columns: 40px 200px;
            column-gap: 20px;
        }

        &-circle {
            position: relative;
            padding: 5px 0 0 11px;
            z-index: 2;
            &::before{
                content: '';
                position: absolute;
                z-index: -1;
                top: 0%;
                left: 0%;
                width: 36px;
                height: 36px;
                border-radius: 100%;
                background: #FFA501;
            }
            
        }
        &-text{
            margin-top: 6px;
            font-size: 14px;
            line-height: 1.5;
            color: #c5c3c3;
           // color: $text-color;
        }
    }
}

@media (max-width: 1200px) {
    .about {
        &__wrapper {
            grid-template-columns: 400px 520px;
            column-gap: 40px;
        }
        &__skills {
            column-gap: 20px;
            grid-template: repeat(2,minmax(115px,auto))/repeat(2,250px);
            &-item {
                grid-template-columns: 40px 195px;
                column-gap: 15px;
            }
        }
    }
}

@media (max-width: 992px) {
    .about {
        &__wrapper {
            grid-template-columns: 320px 320px;
        }
        &-content__size {
            margin: 0 30px;
        }
        &__skills {
            grid-template-columns: 380px;
            grid-auto-rows: minmax(115px,auto);
            &-item {
                grid-template-columns: 40px 250px;
                column-gap: 20px;
            }
        }
        &__photo-border {
            height: 50%;
        }
    }
}

@media (max-width: 768px) {
    .about {
        padding-top: 40px;
        &__wrapper {
            grid-template: 350px auto auto / 1fr;
        }
        &__photo {
            grid-row: unset;
        }
        &__photo {
            width: 320px;
            img {
                width: 100%;
            }
            &-border {
                width: 100%;
                height: 100%;
            }
        }
        &__descr {
            padding-top: 80px;
            width: 80%;
        }
        &__skills {
            grid-template-columns: repeat(2, 260px);
            &-item {
                grid-template-columns: 40px 200px;
                column-gap: 20px;
            }
        }
    }
}

@media (max-width: 576px) {
    .about {
        &__wrapper {
            grid-template: auto auto auto / 1fr;
        }
        &__skills {
            grid-template-columns: 1fr;
        }
        &__descr {
            padding-top: 80px;
            width: 80%;
        }
        &-content__size {
            width: 450px!important;
        }
        &__photo {
            img {
                width: 80%;
            }
            &-border {
                width: 80%;
            }
        }
    }
}

@media (max-width: 406px) {
    .about {
        &__descr {
            padding-top: 30px;
            width: 90%;
        }
        &-content__size {
            width: 320px!important;
            margin: 0 16px;
        }
    }
}