.footer {
    width: 100%;
    height: auto;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    background: linear-gradient(75deg, #d500f9 0%, #4b8ee1 100%);
    &-items{
        text-align: center;
        justify-content: center;
    }
    &-item{
        color: #fff;
        font-size: 42px;
        line-height: 1;
        cursor: pointer;
        transition: all 0.5s;
        &:hover {
            color: #FFA501;
            text-decoration: none;
        }
    }
    &-d__flex {
        display: flex;
        justify-content: space-between;
        //padding-bottom: 20px;
        padding-top: 20px;
    }
    &__social-group {
        display: flex;
        padding-top: 10px;
    }
    &__social-item {
        cursor: pointer;
        background: #000;
        transition: all 1s;
        display: block;
        margin-left: 10px;
        text-align: center;
        padding-top: 3px;
        border-radius: 4px;
        width: 34px;
        height: 34px;
    }
    &-logo {
        color: rgba(255, 255, 255, 0.822)!important;
        position: relative;
        z-index: 2;
        padding-top: 10px;

        font-weight: 500;
        font-size: 18px;
        &::before{
            content: '';
            position: absolute;
            z-index: -1;
            top: 10px;
            right: 80px;
            width: 36px;
            height: 36px;
            border-radius: 100%;
            background: #FFA501;
        }
    }
}
@media (max-width: 840px) {
    .footer {
        &-d__flex {
            display: block;
        }
        &__social-group {
            text-align: center;
            justify-content: center;
            margin-bottom: 20px;
        }
        &-logo {
            //display: inline-block;
            text-align: center;
            justify-content: center;
            margin-right: 20px;
            padding-bottom: 30px;
            &::before{
                display: none;
                content: '';
                position: absolute;
                z-index: -1;
                top: 5px;
                right: 400px;
                width: 36px;
                height: 36px;
                border-radius: 100%;
                background: #FFA501;
            }
        }
    }
}
@media (max-width: 596px) {
    .footer {
        &-item{
            font-size: 25px;
        }
        &-logo {
            font-size: 16px;
            &::before{
                content: '';
                position: absolute;
                z-index: -1;
                top: -3px;
                right: 55px;
                width: 36px;
                height: 36px;
                border-radius: 100%;
                background: #FFA501;
            }
        }
    }
}