.navbars {
    background: #000000;
    box-shadow: 0px 4px 4px rgba(248, 248, 248, 0.308);
    //background: linear-gradient(75deg, #d500f9 0%, #4b8ee1 100%);
    position: fixed;
    width: 100%;
    z-index: 100;
    &-logo {
        color: rgba(255, 255, 255, 0.822)!important;
        margin-right: 50px;
        position: relative;
        z-index: 2;

        font-weight: 500;
        &::before{
            content: '';
            position: absolute;
            z-index: -1;
            top: 3px;
            right: 50px;
            width: 36px;
            height: 36px;
            border-radius: 100%;
            background: #f1c70c;
        }
    }
    &__btns {
        display: flex;
        position: relative;
        top: 10px;
        //padding-top: 10px;
        height: auto;
        box-sizing: border-box;
        text-align: center;
        background: none;
        &-first {
            display: inline-block;
            a {
                border-radius: 6px 0 0 6px;
                background: rgb(0, 0, 0);
                height: 35px;
                padding: 6px;
                color: #fff;
                border: 1px solid #f7eaea3a;
            }
        }
        &-last {
            display: inline-block;
             a {
                border: 1px solid #f7eaea3a;
                border-left: none;
                border-radius: 0 6px 6px 0;
                height: 35px;
                padding: 6px;
            }
        }
    }
    &-link {
        color: #0f8bd3;
        transition: 0.5s all;
        &:hover {
            color: #FFA501;
            text-decoration: none;
        }
    }
    &-btn {
        transition: none;
        outline: none;
        border: none;
        background: linear-gradient(55deg, #d500f9 0%, #FFA501 100%);
    }
}

.modal {
    &-dialog {
        max-width: 500px;
        margin: 170px auto;
    }
    &-content {
        background-color: #000;
    }
    &-title {
        color: #FFA501;
        font-size: 22px;
    }
    label {
        color: #fff;
        font-size: 20px;
    }
    .text-muted {
        color: #6da3d3!important;
        font-size: 18px;
    }
}

.close {
    float: right;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #fff;
    text-shadow: 0 1px 0 #fff;
    opacity: 10;
}

@media (max-width: 992px) {
    .navbars {
        .navbar-collapse {
            padding-bottom: 10px;
        }
        .navbar-nav {
            width: 50%;
        }
        &__btns {
            top: 20px;
            margin-bottom: 40px;
        }
    }
}

@media (max-width: 600px) {
    .navbars {
        .navbar-nav {
            width: 100%;
        }
    }
}