.portfolio {
    padding-top: 120px;
    padding-bottom: 100px;
    background: #000000;
    &-cards__items {
        margin-top: 20px;
        padding: 0 10px;
        display: grid;
       // grid-template: repeat(2, minmax(320px, auto)) / repeat(4, 320px);
       grid-template-columns: 2fr 2fr 2fr 2fr;
       // grid-auto-rows: minmax(200px, auto);
        gap: 13px;
    }
    &-cards{
        position: relative;
        cursor: pointer;
        margin-bottom: 0px;
        margin-top: 20px;
        min-height: 110px;
        width: 100%;
    }
    &-btn {
        padding-top: 10px;
        display: block;
        text-align: center;
        justify-content: center;

        font-size: 18px;
        color: #0467a0;
        //border-bottom: 2px solid #FFA501;
        transition: 0.5s all;
        &:hover {
            text-decoration: none;
            color: #FFA501;
        }
    }
    .face{
        //width: 320px;
        //height: 220px;
        min-height: 200px;
        transition: 0.5s;
    }
    .face1{
        position: relative;
        background-size: cover;
        //background: rgb(255, 255, 255);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
        border-radius: 6px;
        //border-radius: 6px 6px 0 0;
    // transform: translateY(100px);
    }
    &-mirzk {
        background: url('./img/mir-min.jpg') no-repeat;
    }
    &-vash {
        background: url('./img/vas-dom.jpg') no-repeat;
    }
    &-ursus {
        background: url('./img/ursus.jpg') no-repeat;
    }
    &-post {
        background: url('./img/5post.jpg') no-repeat;
    }
    &-ysdk {
        background: url('./img/yuzdek.jpg') no-repeat;
    }
    &-nady {
        background: url('./img/nadj.jpg') no-repeat;
    }
    &-falmec {
        background: url('./img/falm.jpg') no-repeat;
    }
    &-falm-len {
        background: url('./img/falm-lend.jpg') no-repeat;
    }
    &-avtotos {
        background: url('./img/avtoros.jpg') no-repeat;
    }
    &-hyund {
        background: url('./img/hend.jpg') no-repeat;
    }
    &-poll {
        background: url('./img/poll-holl.jpg') no-repeat;
    }
    &-ant {
        background: url('./img/ant-spb.jpg') no-repeat;
    }
    &-trd {
        background: url('./img/tr-d.jpg') no-repeat;
    }
    &-fox {
        background: url('./img/fox26.jpg') no-repeat;
    }
    &-anap {
        background: url('./img/anap.jpg') no-repeat;
    }
    &-ipol {
        background: url('./img/ipol.jpg') no-repeat;
    }
    &-bear {
        background: url('./img/bear.jpg') no-repeat;
    }
    &-mag {
        background: url('./img/mag.jpg') no-repeat;
    }
    &-avtoto {
        background: url('./img/avtoto.jpg') no-repeat;
    }
    &-foods {
        background: url('./img/foods.jpg') no-repeat;
    }
    &-pink {
        background: url('./img/pink.jpg') no-repeat;
    }
    &-irvas {
        background: url('./img/irvas.jpg') no-repeat;
    }
    &-puls {
        background: url('./img/puls.jpg') no-repeat;
    }
    &-ice {
        background: url('./img/ice.jpg') no-repeat;
    }
    &-holst {
        background: url('./img/holst.jpg') no-repeat;
    }
    &-cards:hover .face.face1{
        transform: translateY(0);
    }
    &-contents{
        transition: 0.5s;
    }
    &-cards:hover .face.face1 &-contents{
      opacity: 10;
    }
    
    &-contents p{
        margin: 10px 0 0;
        padding: 0;
        color:#c5c3c3;
        text-align: center;
        font-size: 20px;
    }
    .face2{
        position: relative;
        background: transparent;
        border-radius: 0px 0px 6px 6px;
        //display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px;
        box-sizing: border-box;
       // height: 180px;
       // transform: translateY(-118px);
    }
    &-cards:hover .face.face2{
        height: auto;
        transform: translateY(0);
        border-bottom: 1px solid #0467a0;
        border-left: 1px solid #0467a0;
        border-right: 1px solid #0467a0;
        border-top: none;
        box-shadow: 0 0px 2px #ffa501, 0 2px 2px #ffa501;
    }
    .title__fz36 {
        margin-bottom: 25px;
    }
}

// Accordion

.card {
    background-color: transparent;
    text-align: center;
    color:#c5c3c3;
    font-size: 20px;
    &-header {
        &::after {
            content: "";
            background-image: url('./img/arrow.svg');
            position: absolute;
            margin-left: 5px;
            top: 15px;
            width: 20px;
            height: 30px;
            background-position: 50%;
            background-size: 150%;
        }
    }
    &-body {
        padding: 0;
        text-align: center;
        color: rgb(255, 165, 1);
    }
}


@media (max-width: 1200px) {
    .portfolio {
        &-cards__items {
            grid-template-columns: 3fr 3fr 3fr;
            //grid-template-columns: repeat(auto-fit, 280px);
           // grid-auto-rows: 280px;
            justify-content: space-around;
            row-gap: 35px;
            .horizonal, .vertical {
                grid-row: unset;
                grid-column: unset;
            }
        }
    }
}

@media (max-width: 992px) {
    .portfolio {
        &-cards {
            height: auto;
        }
        &-cards__items {
            grid-template-columns: 2fr 2fr;
           // grid-auto-rows: 370px;
        }
        .face {
            height: 220px;
        }
        .face2 {
            height: 200px;
        }
    }
}

@media (max-width: 768px) {
    .portfolio {
        padding-top: 40px;
    }
}

@media (max-width: 596px) {
    .portfolio {
        &-cards__items {
            grid-template-columns: 1fr;
            //grid-auto-rows: 340px;
        }
        .face2 {
            height: auto;
        }
    }
}