//progressBar

.progressLabel {
    display: none;
}

.progressVisualPart {
    /* Number of the seconds for complete animation */
    transition: width 2s;
    border-radius: 4px;
}

.progressVisualFull {
    display: flex;
    /* change the value if you want different thickness */
    //height: 6px;
    //margin: 20px 0;
    width: 350px;
    height: 14px;
    border-radius: 4px;
    grid-column: 1 / 3;
  }

@media (max-width: 992px) {
    .progressVisualFull {
        width: 280px;
    }
}

@media (max-width: 350px) {
    .progressVisualFull {
        width: 250px;
    }
}