.promo {
    min-height: 100vh;
    max-width: 100vw;
    position: relative;
    background: url('./img/back.jpeg') center no-repeat;
    background-size: cover;
    background-attachment: fixed;
    overflow: hidden;
    &-overlay {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        content: ' ';
        z-index: 2;
        backface-visibility: hidden;
        opacity: 0.5;
        background: linear-gradient(190deg,#2c2c2ce5 0,rgba(252, 207, 8, 0.062) 51.58%);
    }
    &-wrapper {
        position: relative;
       // z-index: 10;
        padding-top: 205px;
        width: 50%;
        z-index: 10;
        padding-left: 15px;
    }
    &__subtitle {
        position: relative;
        padding-left: 0px;
        z-index: 2;
        font-weight: 500;
        &::before{
            content: '';
            position: absolute;
            z-index: -1;
            top: -25%;
            left: 25%;
            width: 36px;
            height: 36px;
            border-radius: 100%;
            background: linear-gradient(25deg, #d500f9 0%, #FFA501 100%);
        }
    }
    &__title {
        margin-top: 25px;
        color: #fff;
        font-size: 48px;
        line-height: 1.1;
    }
    &__btns {
        margin-top: 25px;
        display: flex;
        width: 250px;
        //z-index: 100;
        align-items: center;
        justify-content: space-between;
    }
    &__btn {
        display: block;
        height: 45px;
        color: #fff!important;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
        background: linear-gradient(75deg, #d500f9 0%, #4b8ee1 100%);
        padding: 12px 25px;
        transition: 4s;
    }
   &__link {
        color: #000;
        font-weight: bold;
        &:hover{
            color: #000;
            text-decoration: none;
        }
   }
}

@media (max-width: 1200px) {
    .promo {
        &-wrapper {
            padding-bottom: 40px;
        }
    }
}

@media (max-width: 1024px) {
    .promo {
        min-height: 60vh;
        background-attachment: scroll;
        //background-size: auto 50%;
        background-position-x: 68%;
        background-position-y: 0;
    }
}

@media (max-width: 992px) {
    .promo {
        min-height: 100vh;
        background-size: auto 100%;
        background-position-x: 68%;
        background-position-y: 0px;
    }
}

@media (max-width: 768px) {
    .promo {
        &-wrapper {
            width: 60%;
        }
        &__title {
            line-height: 1.2;
            //width: 500px;
        }
    }
}
@media (max-width: 576px) {
    .promo {
        min-height: 100vh;
        &-wrapper {
            width: 55%;
            padding-top: 205px;
        }
        &__title {
            line-height: 1.2;
           // width: 400px;
            font-size: 30px;
        }
    }
}

@media (max-width: 475px) {
    .promo {
        &-wrapper {
            position: relative;
            padding-bottom: 50px;
            width: 85%;
            padding-top: 180px;
            padding-left: 0;
        }
    }
}