.lookbus {
    overflow-x: hidden;
    margin-top: 80px;
    margin-bottom: 120px;
    .title__section-subtitle {
        color: #000;
        margin-bottom: 30px;
    }
    &-wrapper {
        display: grid;
        grid-template-columns: 2fr 2fr;
        column-gap: 50px;
    }
    &-links {
        display: flex;
        justify-content: space-between;
        width: 50%;
        a {
            text-decoration: none;
            color:#ffa501;
            border-bottom: 1px solid #139ae9;
            transition: all .5s;
            &:hover {
                text-decoration: none;
                color: #139ae9;
                border-bottom: none;
            }
        }
    }
}

.carousel-item {
    height: 300px!important;
}

.carousel-indicators {
    z-index: 0;
    display: none;
}

@media (max-width: 992px)  {
    .lookbus {
        &-wrapper {
            padding:0 20px 0 20px;
            grid-template-columns: 1fr;
            row-gap: 0px;
        }
    }
}