.qa-resume {
    margin-top: 120px;
    .title__section-subtitle {
        color: #000;
    }
    .title {
        color: #000;
    }
    .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
        color: #495057;
        background-color: #fff;
        border-color: #000;
    }
    .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link {
        color: #139ae9 !important;
    }
    .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link:hover{
        border-color: #000;
    }
    .resume__item-location {
        color: #e810f0;
    }
    .resume__item-body {
        color: #000000;
    }
    .resume__item::before {
        background-color: #0a9ef3;
    }
    .resume__column ul li:after {
        background-color: #0a9ef3;
    }
    .skills__item p {
        color: #000;
    }
    .w-100{
        img {
            width: 100%;
        }
    }
    .w-50 {
        img {
            width: 75%;
        }
    }
}