* {
    margin: 0;
    padding: 0;
}

body {
    background: #f2f2f236!important; 
    overflow-x:hidden;
    //background-color: #000;
}


a {
    text-decoration: none;
}

li {
    list-style-type: none;
}
.content {
    max-width: 1340px!important;
    margin: 0 auto;
}

@media (max-width: 1440px) {
    .content {
        width: calc(100% - 60px);
    }
}

@media (max-width: 992px)  {
    .content {
        margin: 0 5px;
    }
}

// Fonts

@import url('https://fonts.googleapis.com/css2?family=Alegreya:ital,wght@1,500&display=swap');

.f-Al {
    font-family: 'Alegreya', serif;
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;700&display=swap');

.f-Pop {
    font-family: 'Poppins', sans-serif;
}


//Colors

.main-color {
    background: #FFA501;
}

.text-color {
    color: #c5c3c3;
}

.dop-style {
    color: #a609ac;
}

.blue {
    color: #0467a0;
}


// Btns

.btn {
    height: auto;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    padding: 12px 25px ;
    transition: 4s; 
}

//Title 

.title{
    font-weight: bold;
    font-size: 36px;
    line-height: 60px;
    color: #c5c3c3;
    &__fz14{
        font-size: 14px;
        line-height: 21px;
    }
    &__fz16{
        font-size: 16px;
        line-height: 24px;
    }
    &__fz20{
        font-size: 20px;
        line-height: 30px;
    }
    &__fz36{
        font-size: 36px;
    }
    &__fz48{
        font-size: 48px;
    }
    &__section-title{
        position: relative;
        text-align: center;
        color: #0467a0;
        padding-top: 2px;
        z-index: 2;
        &:before{
            content: '';
            position: absolute;
            z-index: -1;
            top: -10%;
            left: 49%;
            width: 36px;
            height: 36px;
            border-radius: 100%;
            background: #FFA501;
        }
    }
    &__section-subtitle{
        text-align: center;
    }
}

// Nav-tabs

.nav-tabs {
    justify-content: center;
    border-bottom: none!important;
}
.nav-tabs .nav-link {
    border-radius: 5px!important;
}

.nav-tabs a {
    color: #0467a0;
}

.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
    color: #0467a0!important;
    background-color: transparent!important;
    border-color: #dee2e6 #dee2e6 #fff;
}

@media (max-width: 1200px) {
    .content {
       max-width: 1000px!important;
    }
}

@media (max-width: 992px) {
    .title {
        &_fz36 {
            font-size: 30px;
            line-height: 42px;
            margin-top: 26px;
            margin-bottom: 15px;
        }
    }
    .divider {
        margin: 0 auto;
    }
    .content {
        width: calc(100% - 20px);
        margin: 0 auto;
    }
}

@media (max-width: 768px) {
    .title {
        &_fz48 {
            font-size: 28px;
        }
    }
}

@media (max-width: 576px) {
    .title {
        &_fz48 {
            font-size: 26px;
        }
        &_fz36 {
            font-size: 24px;
            line-height: 36px;
        }
    }
    .hamburger {
        right: 20px;
    }
    .menu {
        &__block {
            width: 250px;
            padding-left: 20px;
        }
    }
    .promo {
        &__btns {
            margin-top: 30px;
        }
    }
    
}

@media (max-width: 406px) {
    .navbar {
        padding: 10px 0 10px 5px !important;
    }
}